import { ADD_WIDGET, UPDATE_WIDGET, UPDATE_LAYOUT } from './actionTypes'

const initialState = {
  widgets: [
    {
      id: 0,
      component: 'chartWidget',
      inUse: false,
      layout: {
        i: "0",
        x: 2,
        y: 1,
        w: 6,
        h: 10,
        minW: 6,
        minH: 10,
        static: false,
        isDraggable: true,
        isResizable: true
      }
    },
    {
      id: 1,
      component: 'testwidget',
      inUse: false,
      layout: {
        i: "1",
        x: 0,
        y: 1,
        w: 2,
        h: 10,
        static: false,
        isDraggable: true,
        isResizable: true
      }
    },
    {
      id: 2,
      component: 'carouselWidget',
      inUse: false,
      layout: {
        i: "2",
        x: 0,
        y: 0,
        w: 10,
        h: 10,
        minH: 10,
        maxH: 10,
        static: false,
        isDraggable: true,
        isResizable: true
      }
    },
    {
      id: 3,
      component: 'TestWidget3',
      inUse: false,
      layout: {
        i: "3",
        x: 10,
        y: 0,
        w: 2,
        h: 10,
        static: false,
        isDraggable: true,
        isResizable: true
      }
    },
    {
      id: 4,
      component: 'TestWidget4',
      inUse: false,
      layout: {
        i: "4",
        x: 8,
        y: 1,
        w: 4,
        h: 10,
        static: false,
        isDraggable: true,
        isResizable: true
      }
    }
  ]
};

function reducer(state = initialState, action) {
  switch (action.type) {
    case ADD_WIDGET:
      // console.log("Before");
      // console.log(state.widgets)
      return { widgets: 
        state.widgets.map(widget => {
          if (widget.id !== action.payload.id) {
            // This isn't the item we care about - keep it as-is
            return widget
          }
          // Otherwise, this is the one we want - return an updated value
          return {
            ...widget,
            ...action.payload.widget
          }
        })
      } 
    default:
      // console.log("after");
      // console.log(state.widgets)
      return state;
  }
}
export default reducer;