const mapStateToProps = (state) => {
  // console.log("State");
  // console.log(state);
  var dashboardWidgets= state.widgets.filter(widget => widget.inUse === true);
  console.log("Dashboard");
  console.log(dashboardWidgets);
  return { widgets: dashboardWidgets }
}

export default connect(
  mapStateToProps,
  {
    addWidget: addWidget
  }
  )(DashboardBasicGridLayout);